import { Component, OnDestroy, OnInit, ViewContainerRef, Renderer2, NgModule, ViewChild, ElementRef } from '@angular/core';
import { HttpClient, HttpErrorResponse, HttpResponse } from '@angular/common/http';
import { FormGroup, FormBuilder, FormControl, Validators, FormArray } from '@angular/forms';
import { Router } from '@angular/router';
import { Inject, Injectable } from '@angular/core';
import { DOCUMENT } from '@angular/platform-browser';
import { AppService } from '../app.service';

@Injectable()
@Component({
	selector: 'app-company-details',
	templateUrl: './company-details.component.html',
	styleUrls: ['./company-details.component.css']
})
export class CompanyDetailsComponent implements OnInit {
	public companyName: string;
	public companyIds: string;
	public companyIdArr: any;
	
	constructor(
		private formBuilder: FormBuilder,
		private http: HttpClient,
		private router: Router,
		private renderer: Renderer2,
		private elementRef: ElementRef,
		@Inject(DOCUMENT) private document: any,
		private appService: AppService
	) {

	}

	public ngOnInit(): any {
		this.appService.currentCompanyName.subscribe(companyName => this.companyName = companyName);
		this.appService.currentCompanyIds.subscribe(companyIds => this.companyIds = companyIds);
		if(this.companyName == undefined || this.companyName == ''){
			this.router.navigateByUrl('/companysearch');
		}
		this.companyIds = this.companyIds.substring(0, this.companyIds.length - 1);
		this.companyIdArr = this.companyIds.split(",");
		console.log(this.companyIdArr);
	}	
}