import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
@Injectable()
export class AppService {
	private companyName = new BehaviorSubject<string>('');
	currentCompanyName = this.companyName.asObservable();

	private companyIds = new BehaviorSubject<string>('');
	currentCompanyIds = this.companyIds.asObservable();

	constructor() { }

	setCompanyName(companyName: string) {
		this.companyName.next(companyName);
		console.log(this.companyName);
	}

	setCompanyIds(companyIds: string) {
		this.companyIds.next(companyIds);
	}
}